<template>
  <div class="addActivity">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <!-- 标题 -->
      <el-form-item label="标题：" prop="name">
        <el-input v-model="ruleForm.name" placeholder="活动标题"></el-input>
      </el-form-item>
      <!-- 描述 -->
      <el-form-item label="描述：" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc" placeholder="活动描述"></el-input>
      </el-form-item>
      <!-- 上传图片 -->
      <el-form-item label="图片：" prop="picture">
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="3"
          :on-exceed="handleExceed"
          :auto-upload="false"
          :file-list="fileList"
        >
          <el-button size="small" type="primary">选中图片</el-button>
          <div slot="tip" class="el-upload__tip">建议图片上传尺寸：750px*410px</div>
        </el-upload>
      </el-form-item>
      <!-- 活动创建者 -->
      <el-form-item label="活动创建者：" prop="who">
        <el-input v-model="ruleForm.who" placeholder="活动创建者"></el-input>
      </el-form-item>
      <!-- 活动类型 -->
      <el-form-item label="活动类型：" prop="type">
        <el-select v-model="ruleForm.typeValue">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <!-- 活动时间 -->
      <el-form-item label="活动时间:" prop="date">
        <div class="block">
          <el-date-picker
            v-model="ruleForm.dateValue"
            type="daterange"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
        </div>
      </el-form-item>
      <!-- 活动链接 -->
      <el-form-item label="活动链接:" prop="actlink">
        <el-input placeholder="活动链接" v-model="ruleForm.actlinkValue">
        </el-input>
      </el-form-item>
      <!-- 渠道 -->
      <el-form-item label="渠道：">
        <div class="chan-box">
          <el-checkbox v-model="ruleForm.CheckedWeChat">微信</el-checkbox>
          <el-input placeholder="渠道链接" v-model="ruleForm.WeChartlink"></el-input>
        </div>
        <div class="chan-box">
          <el-checkbox v-model="ruleForm.checkedQQ">QQ</el-checkbox>
          <el-input placeholder="渠道链接" v-model="ruleForm.QQlink"></el-input>
        </div>
        <div class="chan-box">
          <el-checkbox v-model="ruleForm.checkedWeibo">微博</el-checkbox>
          <el-input placeholder="渠道链接" v-model="ruleForm.Weibolink"></el-input>
        </div>
      </el-form-item>
      <!-- SEO -->
      <el-form-item label="SEO关键字：">
        <span class="suv">SUV</span>
        <el-button plain @click="dialogVisible = true">+</el-button>
        <el-dialog
          :visible.sync="dialogVisible"
          width="25%"
          center>
          <el-input v-model="SUVval"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
      </el-form-item>
      <el-form-item label="SEO描述：">
        <el-input v-model="ruleForm.SEOdesc" placeholder="SEO描述"></el-input>
      </el-form-item>
      <el-form-item label="接口文档：">
        <el-button @click="generateDoc">生成文档</el-button><br />
        <span>生成标题接口文档</span>
        <a class="down">下载</a>
      </el-form-item>
      <el-form-item label="标签：">
        <span class="suv">SUV</span>
        <el-button plain @click="dialogVisible = true">+</el-button>
        <el-dialog
          :visible.sync="dialogVisible"
          width="25%"
          center>
          <el-input v-model="SUVval2"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
      </el-form-item>
      <div class="btns-box">
        <div class="wrap-btns">
          <el-button plain>浏览</el-button>
          <el-button plain>保存</el-button>
        </div>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'AddActivity',
  data() {
    return {
      dialogVisible: false,
      SUVval: '',
      SUVval2: '',
      ruleForm: {
        name: '',
        desc: '',
        who: '',
        typeValue: '类型1',
        dateValue: '',
        actlinkValue: '',
        CheckedWeChat: false,
        checkedQQ: false,
        checkedWeibo: false,
        WeChartlink: '',
        QQlink: '',
        Weibolink: ''
      },
      rules: { // 校验规则
        name: [ // 标题
          { required: true, message: '请输入活动标题', trigger: 'blur' }
        ],
        desc: [ // 活动表述
          { required: true, message: '请填写活动描述', trigger: 'blur' }
        ],
        picture: [ // 上传图片
          { required: true, message: '请上传图片' }
        ],
        who: [ // 活动创建者
          { required: true, message: '请输入活动创建者', trigger: 'blur' }
        ],
        type: [ // 活动类型
          { required: true, message: '请选择活动类型', trigger: 'select' }
        ],
        date: [ // 活动时间
          { required: true, message: '请选择活动时间' }
        ],
        actlink: [ // 活动链接
          { required: true }
        ]
      },
      // 上传图片
      fileList: [
        { name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100' },
        { name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100' }
      ],
      // 活动类型
      options: [{
        value: '选项1',
        label: '类型1'
      }, {
        value: '选项2',
        label: '类型2'
      }, {
        value: '选项3',
        label: '类型3'
      }, {
        value: '选项4',
        label: '类型4'
      }]
    }
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePreview(file) {
      console.log(file)
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`)
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除${file.name}？`)
    },
    generateDoc() {
      this.$router.push({
        name: 'generateDoc'
      })
    }
  }
}
</script>

<style lang="scss">
  .chan-box {
    display: flex;
    .el-input__inner {
      height: 30px;
    }
  }
  .addActivity .el-form-item {
    width: 700px;
  }
  .addActivity .el-upload__tip {
    color: #f00;
  }
  .addActivity .el-checkbox:last-of-type {
    width: 80px;
  }
  .addActivity .suv {
    margin-right: 10px;
  }
  .btns-box {
    width: 100%;
    .wrap-btns {
      width: 300px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      .el-button {
        width: 120px;
      }
    }
  }
  .down {
    margin-left: 80px;
  }
</style>
